.gpt3__blog-container_article {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: var(--color-footer);
}
.gpt3__blog-container_article-image {
  width: 100%;
  height: 100%;
  background: var(--color-footer);
}

.gpt3__blog-container_article-image img {
  width: 100%;
  height: 100%;
}

.gpt3__blog-container_article-content {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  padding: 1rem 1.5rem;
  height: 100%;
}

.gpt3__blog-container_article-content p {
  font-weight: bold;
  line-height: 35px;
  font-size: 11px;
  color: #fff;
}
.gpt3__blog-container_article-content h3 {
  cursor: pointer;
  font-size: 25px;
  font-weight: 800;
  line-height: 30px;
  color: #fff;
  margin-bottom: 5rem;
}
.gpt3__blog-container_article-content p:last-child {
    cursor: pointer;
}

@media screen and (max-width:550px) {
    .gpt3__blog-container_article-content h3{
        font-size: 18px;
        line-height: 25px;
    }
}
